.auth-scheme-tree-wrap .el-collapse {
  border: none;
}
.auth-scheme-tree-wrap .el-collapse-item__header {
  height: auto;
  background-color: transparent;
  line-height: 25px;
  padding-bottom: 10px;
}
.auth-scheme-tree-wrap .el-collapse-item__arrow {
  display: none;
}
.auth-scheme-tree-wrap .el-radio__inner {
  border: none;
  background: url("../../../assets/img/icons/radio-unchecked.png") no-repeat !important;
  background-size: 100% !important;
}
.auth-scheme-tree-wrap .el-radio__input.is-checked + .el-radio__label {
  color: #89D1AC !important;
}
.auth-scheme-tree-wrap .el-radio__input.is-checked .el-radio__inner {
  background-image: url("../../../assets/img/icons/radio-checked.png") !important;
}
.auth-scheme-tree-wrap .el-radio__input.is-checked .el-radio__inner:after {
  display: none;
}
